import { Component } from '@angular/core';
import { MapsService } from '../services/map.service';
import { map, filter } from 'rxjs/operators';

import { Socket } from 'ng-socket-io';
import { SocketService } from '../services/socket.service';
import { FormArrayName } from '@angular/forms';


@Component({
  selector: 'app-tab2',
  templateUrl: 'tab2.page.html',
  styleUrls: ['tab2.page.scss']
})

export class Tab2Page {
    lat: string = '';
    lng: string = '';
    markers: any = [];
    location : Object;
    markz: any;
    icon: any;
    //socket:any;
   // markers: Array<any> = [];

    constructor( private map: MapsService, private socket: Socket, public socketService: SocketService) {
      
    }

    ionViewDidEnter(){
      this.showMap();
      this.socket.emit('getImgsMap');
     //loading images to map
       this.socket.on('imagesMap', (images) => {
        this.markers = images;
        console.log(this.markers);
        console.log('markers');
      });
    }

//Shows current position
  showMap(){
      this.map.getLocation().subscribe(data => {
          console.log(data);
          this.lat = data.latitude;
          this.lng = data.longitude;
      }); 
  }
}